import React, { useEffect } from 'react';
import './section2.scss';
import Swiper from 'swiper';




export default function Section2() {


    return (
        <section className="section_2 container-fluid">
            <div className="row justify-content-center">
                <div className="col-sm-8">
                    <h2 className="text-center pt-5">
                        Clinic and Specialities
          </h2>
                    <h6 className="text-center text-black-50">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.

               </h6>
                </div>
            </div>
            <div className="row justify-content-center">


                <SwiperSlide colclass="col-11 col-sm-9" />



            </div>

        </section>
    )
}



const SwiperSlide = ({ colclass }) => {
    const swiperObj = [
        {
            id: 1,
            img: require('./img/specialities-01.png'),
            pro: 'Urology'

        },
        {
            id: 2,
            img: require('./img/specialities-02.png'),
            pro: 'Neurology'

        },
        {
            id: 3,
            img: require('./img/specialities-03.png'),
            pro: 'Orthopedic'

        },
        {
            id: 4,
            img: require('./img/specialities-04.png'),
            pro: 'Cardiologist'

        },
        {
            id: 5,
            img: require('./img/specialities-05.png'),
            pro: 'Dentist'

        },

        {
            id: 7,
            img: require('./img/specialities-01.png'),
            pro: 'Urology'

        },

        {
            id: 8,
            img: require('./img/specialities-02.png'),
            pro: 'Neurology'

        },
        {
            id: 9,
            img: require('./img/specialities-03.png'),
            pro: 'Orthopedic'

        }
    ]
    useEffect(() => {

        let swiper1 = new Swiper('.swiper1', {

            slidesPerGroup: 1,
            loop: true,
            loopFillGroupWithBlank: true,
            breakpoints: {
                500: {
                    slidesPerView: 2,
                    spaceBetween: 20,
                },
                700: {
                    slidesPerView: 3,
                    spaceBetween: 10,
                },

                900: {
                    slidesPerView: 3.6,
                    spaceBetween: 10,
                }
            },

            pagination: {
                el: '.swiper-pagination1',
                clickable: true,
            },
            navigation: {
                nextEl: '.swiper-button-next1',
                prevEl: '.swiper-button-prev1',
            },
        });


    }, []);



    return (

        <div className={`swiper-container swiper1 ${colclass}`}>
            <div className="swiper-wrapper">
                {
                    swiperObj.map(v => {
                        return (

                            <div key={v.id} className="swiper-slide">

                                <div>
                                    <div className="item d-flex justify-content-center align-items-center mb-4">

                                        <img  src={v.img} alt="" />
                                        <span id="item_point"></span>
                                    </div>
                                    <h5>
                                        {v.pro}
                                    </h5>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            <div className="swiper-pagination swiper-pagination1"></div>

        </div>


    )
}