import React, { useState, useEffect, useRef  } from 'react';
import './schedule.scss';
import { ProfileAside } from '../appointment/appointment';

export default function Schedule(props) {
    return (
        <React.Fragment>
            <div className="schedule_page">
                <div className="container-fluid top_control  footer_bg_color">
                    <div className="row p-2">
                        <div className="col">
                            <a href="#">Home</a> /
                <a href="#">Schedule</a>
                            <br />
                            <h4>
                                Schedule
                </h4>
                        </div>
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="row justify-content-around">
                        <ProfileAside />
                        <Schedule_Time />


                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}




function Schedule_Time() {
    const [slot, setSlot] = useState(false);
    const [sunday, setSunday] = useState(true);
    const [monday, setMonday] = useState(false);
    const [tuesday, setTuesday] = useState(false);
    const [wednesday, setWednesday] = useState(false);
    const [thursday, setThursday] = useState(false);
    const [friday, setFriday] = useState(false);
    const [saturday, setSaturday] = useState(false);
    const btnSun = () => {
        if (monday || tuesday || wednesday || thursday || friday || saturday) {
            setSunday(true)
            setMonday(false);
            setTuesday(false);
            setWednesday(false);
            setThursday(false);
            setFriday(false);
            setSaturday(false);
        }
    }

    const btnMon = () => {
        if (sunday || tuesday || wednesday || thursday || friday || saturday) {
            setSunday(false)
            setMonday(true);
            setTuesday(false);
            setWednesday(false);
            setThursday(false);
            setFriday(false);
            setSaturday(false);
        }
    }

    const btnTue = () => {
        if (sunday || monday || wednesday || thursday || friday || saturday) {
            setSunday(false)
            setMonday(false);
            setTuesday(true);
            setWednesday(false);
            setThursday(false);
            setFriday(false);
            setSaturday(false);
        }
    }

    const btnWed = () => {
        if (sunday || monday || tuesday || thursday || friday || saturday) {
            setSunday(false)
            setMonday(false);
            setTuesday(false);
            setWednesday(true);
            setThursday(false);
            setFriday(false);
            setSaturday(false);
        }
    }

    const btnThu = () => {
        if (sunday || monday || tuesday || wednesday || friday || saturday) {
            setSunday(false)
            setMonday(false);
            setTuesday(false);
            setWednesday(false);
            setThursday(true);
            setFriday(false);
            setSaturday(false);
        }
    }

    const btnFri = () => {
        if (sunday || monday || tuesday || wednesday || thursday || friday || saturday) {
            setSunday(false)
            setMonday(false);
            setTuesday(false);
            setWednesday(false);
            setThursday(false);
            setFriday(true);
            setSaturday(false);
        }
    }

    const btnSat = () => {
        if (sunday || monday || tuesday || wednesday || thursday || friday || saturday) {
            setSunday(false)
            setMonday(false);
            setTuesday(false);
            setWednesday(false);
            setThursday(false);
            setFriday(false);
            setSaturday(true);
        }
    }
   


    return (
        <div className=" col-12 col-md-7 p-3 my-3 ml-md-4 rounded border  appointment_profile">
            <div className="time_set">
                <h5>Schedule Time</h5>
                <p>Timing Slot duration</p>
                <select name="" id="" value>
                    <option value="">-</option>
                    <option value="">15 mins</option>
                    <option value="" >30 mins</option>
                    <option value="">45 mins</option>
                    <option value="">1 Hour</option>
                </select>
            </div>

            <div className="schedule_setTime p-3 border my-3">
                <button onClick={btnSun} className="btn btn-danger m-2">SUNDAY</button>
                <button onClick={btnMon} className="btn btn-danger m-2">MONDAY</button>
                <button onClick={btnTue} className="btn btn-danger m-2">TUESDAY</button>
                <button onClick={btnWed} className="btn btn-danger m-2">WEDNESDAY</button>
                <button onClick={btnThu} className="btn btn-danger m-2">Thursday</button>
                <button onClick={btnFri} className="btn btn-danger m-2">FRIDAY</button>
                <button onClick={btnSat} className="btn btn-danger m-2">SATURDAY</button>
            </div>

            <div className="schedule_result my-3 p-3 border">
                {/* Sunday wrapper */}
                {sunday ?
                    <div  className="sunday">
                        <div className="row no-gutters  justify-content-between">
                            <span>Time Slots</span>
                            <button  onClick={() => setSlot(!slot)} className="outline-0  bg-light border-0">  <i className="fas fa-plus-circle    "></i> Add slot </button>
                        </div>
                        <div className="row no-gutters">
                            Not Available
             </div>
                        <Btn_slot slot={slot} setSlot={setSlot} />
                    </div>
                    : null

                }

                {/* Monday wrapper */}
                {monday ?

                    <div className="monday">
                        <div className="row no-gutters justify-content-between">
                            <span>
                                Time Slots
                    </span>
                            <button className="outline-0 border-0">
                                <i className="fas fa-edit    "></i>
                        Edit
                    </button>
                        </div>
                        <div className="row no-gutters">
                            <div className="col">
                                <span> 8:00 pm - 11:30 pm </span>
                                <span><i className="fas fa-times    "></i></span>


                            </div>
                            <div className="col">
                                <span>11:30 pm - 1:30 pm </span>
                                <span><i className="fas fa-times    "></i></span>

                            </div>
                            <div className="col">

                                <span>3:00 pm - 5:00 pm </span>
                                <span><i className="fas fa-times    "></i></span>
                            </div>
                            <div className="col">

                                <span>6:00 pm - 11:00 pm </span>
                                <span><i className="fas fa-times    "></i></span>
                            </div>
                        </div>
                    </div>

                    : null}
                {/* Tuesday */}
                {tuesday ?

                    <div className="tuesday">
                        <div className="row no-gutters justify-content-between">
                            <span>Time Slots
</span>
                            <button><i className="fas fa-plus    "></i> add slot</button>
                            tuesday
                        </div>

                    </div> : null
                }

                {/* Wednesday */}
                {wednesday ?
                    <div className="wednesday">
                        <div className="row no-gutters justify-content-between">
                            <span>Time Slots
</span>
                            <button><i className="fas fa-plus    "></i> add slot</button>
                            wednesday
                        </div>

                    </div> : null
                }

                {/* Thursday */}
                {thursday ?

                    <div className="thursday">
                        <div className="row no-gutters justify-content-between">
                            <span>Time Slots
</span>
                            <button><i className="fas fa-plus    "></i> add slot</button>
                            thursday
                        </div>

                    </div> : null
                }
                {/* Friday */}
                {friday ?
                    <div className="friday">
                        <div className="row no-gutters justify-content-between">
                            <span>Time Slots
</span>
                            <button><i className="fas fa-plus    "></i> add slot</button>
                            friday
                        </div>

                    </div>
                    : null
                }
                {/* Sunday */}
                {saturday ?

                    <div className="saturday">
                        <div className="row no-gutters justify-content-between">
                            <span>Time Slots
</span>
                            <button><i className="fas fa-plus    "></i> add slot</button>
                            Saturday
                        </div>

                    </div> : null

                }


            </div>


        </div>


    )
}




const Btn_slot = ({ slot, setSlot }) => {

    const [slot_set, setSlot_set] = useState('');
    const [removeRow, setRemoveRow] = useState(false);

    const deleteRow = <button onClick={()=> setRemoveRow(true)} type="button" className="btn btn-danger btn-sm"><i className="fas fa-trash    "></i></button>
    const add_more_slot = () => {
        const newSlot =  <Slot_schedule_set mykey={Math.random()} btnDelete={deleteRow} />


        
        setSlot_set([...slot_set, newSlot])
    }






    return (
        <div  className={slot ? 'btn_slot slot_show' : ' slot_hide'}>
            <div className="container-fluid h-100">
                <div className="row h-100 justify-content-center align-items-center">
                    <div className=" card_slot bg-light text-dark col-10 col-sm-8 col-md-5 p-3 rounded-lg shadow">
                        <div className="row no-gutters justify-content-between my-2 align-items-center">
                            <span>Add Time Slots </span>
                            <button className="btn btn-primary" onClick={() => setSlot(false)}><i className="fas fa-times    "></i></button>
                        </div>
                        <Slot_schedule_set/>
                        
                   {slot_set}

                        <div className="row no-gutters">
                            <button onClick={add_more_slot} className="btn btn-danger btn-sm">
                                <i className="fas fa-plus    "></i> Add more </button>
                        </div>
                        <div className="row no-gutters justify-content-center my-2">
                            <button type="button" className="btn btn-primary">Save Now</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const Slot_schedule_set = (props) => {
  
    return (

        <>

        
        
        <div className="row no-gutters my-2">

            <div className="col-5 pr-1 m-0">
                <span>Start Time</span>
                <select className="d-block w-100" name="" id="">
                    <option value="">-</option>
                    <option value="">12:00 am</option>
                    <option value="">12:30 am</option>
                    <option value="">1:00 am</option>
                    <option value="">1:30 am</option>
                </select>
            </div>
            <div className="col-5 m-0 ">
                <span>End Time</span>
                <select className="d-block w-100" name="" id="">
                    <option value="">-</option>
                    <option value="">12:00 am</option>
                    <option value="">12:30 am</option>
                    <option value="">1:00 am</option>
                    <option value="">1:30 am</option>
                </select>
            </div>
            <div className="col-2 px-1 align-self-end">
                {props.btnDelete}

            </div>
        </div>


        </>
    )
}