import React, { useEffect } from 'react';
import Swiper from 'swiper';
import './section4.scss';

export default function Section4() {
    return (
        <section className="section_4 py-5 container-fluid">
            <div className="row">
                <div className="col-sm-5">
                    <img src={require('./img/feature.png')}
                        className="d-block w-100" alt="" />

                </div>

                <div className="col-sm-7">
                    <h2>
                        Availabe Features in Our Clinic
                    </h2>
                    <h6 className="text-black-50">
                        It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout.
                    </h6>

                    <SwiperSlide />

                </div>
            </div>

        </section>
    )
}

const SwiperSlide = () => {
    useEffect(() => {
        var swiper2 = new Swiper('.swiper3', {
            slidesPerGroup: 1,
            loop: true,
            loopFillGroupWithBlank: true,
            breakpoints: {
                640: {
                    slidesPerView: 2,
                    spaceBetween: 40,
                },

                850: {
                    slidesPerView: 3,
                    spaceBetween: 50,
                }
            },

            pagination: {
                el: '.swiper-pagination3',
                clickable: true,
            },
            navigation: {
                nextEl: '.swiper-button-next3',
                prevEl: '.swiper-button-prev3',
            },
        });
    }, []);

    const swiperObj = [
        {
            id: 1,
            img: require('./img/feature-06.jpg'),
            pro: 'Medical'

        },
        {
            id: 2,
            img: require('./img/feature-01.jpg'),
            pro: 'Patient Word'

        },
        {
            id: 3,
            img: require('./img/feature-02.jpg'),
            pro: 'Test Room'

        },
        {
            id: 4,
            img: require('./img/feature-03.jpg'),
            pro: 'Laboratory'

        },
        {
            id: 5,
            img: require('./img/feature-04.jpg'),
            pro: 'Operation'

        },
        {
            id: 6,
            img: require('./img/feature-05.jpg'),
            pro: 'Patient Ward'

        },
    ]


    return (

        <div className="swiper-container swiper3">
            <div className="swiper-wrapper ">

                {
                    swiperObj.map(v => {
                        return (
                            <div key={v.id} className="swiper-slide">
                                <div>
                                    <div className="item d-flex justify-content-center align-items-center mb-4">
                                        <img className="d-block w-100 rounded-circle" src={v.img} alt="" />
                                    </div>
                                    <h5>
                                        {v.pro}

                                    </h5>

                                </div>
                            </div>
                        )
                    })
                }


            </div>

            <div className="swiper-pagination swiper-pagination3"></div>

        </div>
    )
}
