import React, { useState } from 'react';
import './dashboard.scss';
//import Appointment from '../appointment/appointment';
import { ProfileAside } from '../appointment/appointment';
import Circle from 'react-circle'; // source: https://reactjsexample.com/renders-a-svg-circle-progress/

export default function Dashboard() {

    const msg_notice = <span className="badge badge-pill badge-primary ml-auto"> 23</span>;


    return (
        <>
            <div className="container-fluid top_control  footer_bg_color">
                <div className="row p-2">
                    <div className="col">
                        <a href="#">Home</a> /
                    <a href="#">Dashboard</a>
                        <br />
                        <h4>
                            Dashboard
                    </h4>
                    </div>
                </div>
            </div>

            <div className="container-fluid">

                <div className="row justify-content-around">
                    <ProfileAside msg={msg_notice} />
                    <DoctorList />
                </div>
            </div>

        </>
    );
}

const DoctorList = ({ cardObj }) => {
    const topLineCard = [
        {
            id: 1,
            title: 'Total Patient',
            count: '1500',
            date: 'Till Today',
            progress: 90,
            animate: true,
            progressColor: '#DA3F81',
            showPercentage: false,
            circleImg: 'https://dreamguys.co.in/demo/doccure/template/assets/img/icon-01.png'
        },
        {
            id: 2,
            title: 'Total Patient',
            count: '160',
            date: '06, Nov 2019',
            progress: 50,
            animate: true,
            progressColor: '#68DDA9',
            showPercentage: false,
            circleImg: 'https://dreamguys.co.in/demo/doccure/template/assets/img/icon-02.png'
        },
        {
            id: 3,
            title: 'Appoinments',
            count: '85',
            date: '06, Apr 2019',
            progress: 70,
            animate: true,
            progressColor: '#1B5A90',
            showPercentage: false,
            circleImg: 'https://dreamguys.co.in/demo/doccure/template/assets/img/icon-03.png'
        },

    ]


    return (

        <div className=" col-12 col-md-7  my-3  ">
            <div className="row no-gutters border mb-4">
                {topLineCard.map(value => {
                    return (
                        <div key={value.id} className="col-sm-4  circle_wrapper  py-4 m-0 text-center">
                            <div className="circle position-relative">

                                <Circle
                                    progress={value.progress}
                                    animate={value.animate}
                                    progressColor={value.progressColor}
                                    showPercentage={value.showPercentage}
                                />
                                <img className="position_center"
                                    src={value.circleImg} alt=""
                                />
                            </div>
                            <div> {value.title} </div>
                            <div className="patient_count"> {value.count} </div>
                            <div className="date"> {value.date} </div>
                        </div>
                    )
                })}
            </div>
            <div className="row no-gutters">
                <Doctor_List_Table />
            </div>


        </div>
    )
}

